<template>
  <div class="v-leftbar">
    <Logo></Logo>
    <ul class="v-leftbar-ul" v-for="ul in leftbarData">
      <h3 class="tle">{{ul.name}}</h3>
      <LeftItem :type="li.icon" :route="li.route" v-for="li in ul.items" :key="li.name">
        {{li.name}}
      </LeftItem>
    </ul>
  </div>
</template>

<script type='text/ecmascript-6'>
import Logo from './logo.vue'
import LeftItem from './left-item.vue'
export default {
  components: {
    Logo, LeftItem
  },
  data () {
    return {
      leftbarData: [
        {
          name: '让我们开始吧',
          route: '',
          items: [
            {
              name: '快速上手',
              icon: 'judge',
              route: '/start/install'
            }
          ]
        },
        {
          name: '组件',
          route: '',
          items: [
            {
              name: 'Grid 栅格布局',
              icon: 'memo',
              route: '/components/grid'
            },
            {
              name: 'Toast 提示信息',
              icon: 'comment',
              route: '/components/toast'
            },
            {
              name: 'Alert 警告框',
              icon: 'warning',
              route: '/components/alert'
            }
          ]
        },
        {
          name: '工具',
          items: [
            {
              name: 'DOM操作方法',
              icon: 'spanner',
              route: '/tools/dom'
            }
          ]
        }
      ]
    }
  }
}
</script>

<style lang='stylus' rel='stylesheet/stylus'>
@import "../styl/constant.styl"
.v-leftbar
  width: 17%
  border-right: 1px solid darkBorder
  .v-leftbar-ul
    font-size: 16px
    margin: 8px 0
    cursor: default
    .tle
      height: 48px
      line-height: 48px
      font-size: 14px
      padding-left: 28px
      color: blackText
</style>



