<template>
  <div class="v-displaybox">
    <div class="left">
      <div class="display">
        <slot name="display"></slot>
      </div>
      <header class="header">
        <span>{{header}}</span>
      </header>
      <div class="desc">
        {{desc}}
      </div>
    </div>
    <div class="right" ref="codeContainer">
      <slot class="code" name="code" ref="code"></slot>
      <div class="more" @click="moreCode" v-if="needFoldBtn">
        <vcIcon class="icon" :type="codeArrow"></vcIcon>
      </div>
    </div>
  </div>
</template>

<script type='text/ecmascript-6'>
  export default {
    props: {
      header: {
        type: String,
        default: ''
      },
      desc: {
        typs: String,
        default: ''
      }
    },
    data () {
      return {
        codeArrow: 'arrow-down',
        codeZone: {},
        foldHeight: '',
        unfoldHeight: '',
        needFoldBtn: false,
        isFold: true
      }
    },
    methods: {
      moreCode () {
        window.vc.dom.setStyle(this.$refs.codeContainer, 'height', this.isFold ? this.foldHeight : this.unfoldHeight)
        this.isFold = !this.isFold
        setTimeout(() => {
          this.codeArrow = !this.isFold ? 'arrow-up' : 'arrow-down'
        }, 100)
      },
      // 计算px为单位的数值
      pxCal (num1, num2, resultNeedPx) {
        const cal = num => num.split('px')[0] - 0
        return resultNeedPx ? cal(num1) - cal(num2) + 'px' : cal(num1) - cal(num2)
      }
    },
    mounted () {
      let codeContainer = this.$refs.codeContainer
      this.codeZone = codeContainer.querySelector('section')
      this.foldHeight = window.vc.dom.getStyle(this.codeZone, 'height')
      this.unfoldHeight = window.vc.dom.getStyle(codeContainer, 'height')
      if (this.pxCal(this.foldHeight, this.unfoldHeight) > 20) this.needFoldBtn = true
    }
  }
</script>

<style lang='stylus' rel='stylesheet/stylus'>
@import "../styl/constant.styl"
.v-displaybox
  position: relative
  width: 100%
  border: 1px solid #eee
  border-radius: 6px
  margin-bottom: 20px
  overflow: hidden
  &:after
    clear: both
    content: ''
    display: block
  &:hover
    box-shadow: 0 2px 7px rgba(0,0,0,.15)
    border-color: transparent
  .left
    float: left
    width: 50%
    border-right: 1px dotted #eee
    overflow: hidden
    .display
      width: 100%
      padding: 10px 10px 15px
    .header
      position: relative
      &:before
        content:''
        display: block
        width: 100%
        height: 1px
        background: #eee
        position: absolute
        top: 10px
        left: 0
      span
        display: inline-block
        background: #fff
        padding: 0 5px 0 12px
        position: relative
        margin-left: 30px
        font-size: 14px
    .desc
      padding: 10px 10px 15px
      word-break: break-all
  .right
    width: 49%
    float: right
    padding: 10px 5px
    height: 0
    transition: height .2s ease-in-out
    section
      width: 100%
      overflow-x: scroll
    .more
      position: absolute
      bottom: 0
      margin-left: -15px
      width: 50%
      box-shadow: inset 0 -15px 30px #fff
      text-align: center
      cursor: pointer
      .icon
        line-height: 1
        font-size: 5px
        font-weight: 100
        color: #ccc
      &:hover
        .icon
          color: darkAlphaColor !important
</style>



