<template>
  <div @click="openAlpha" class="v-logo" >
    <img class="logo" src="../assets/Alpha-logo-orange.png" alt="logo">
    <h3 class="tle text-overflow">iCourt</h3>
    <p class="slogan text-overflow">不止律所管理系统</p>
  </div>
</template>

<script type='text/ecmascript-6'>
  export default {
    methods: {
      openAlpha () {
        window.open('https://www.alphalawyer.cn')
      }
    }
  }
</script>

<style lang='stylus' rel='stylesheet/stylus'>
@import "../styl/constant.styl"
.v-logo
  cursor pointer
  width: 80%
  margin: 20px 20px 40px
  padding: 10px
  display: block
  position: relative
  top: 20px
  border: 1px solid transparent
  border-radius: 12px
  transition all 1s
  &:hover
    border: 1px solid lightAlphaColor
  .logo
    border: none
    width: 40px
    height: 40px
    float: left
    box-sizing: border-box
    padding: 1% 5px 0 0
    margin-right 10px
    .tle
      font-size: 16px
      &:active
        text-decoration none
    .slogan
      font-size: 12px

</style>



