import Vue from 'vue'
import Router from 'vue-router'
import Install from '../components/routers/start/install.vue'
import Alert from '../components/routers/components/alert/alert.md'
import Toast from '../components/routers/components/toast/toast.md'
import Grid from '../components/routers/components/grid/grid.md'
import Dom from '../components/routers/tools/dom/dom.md'

Vue.use(Router)

const routes = [
  {
    path: '/',
    redirect: '/start/install'
  },
  {
    path: '/start/install',
    component: Install
  },
  {
    path: '/components/alert',
    component: Alert
  },
  {
    path: '/components/toast',
    component: Toast
  },
  {
    path: '/components/grid',
    component: Grid
  },
  {
    path: '/tools/dom',
    component: Dom
  }
]

export default new Router({
  routes
})



