<template>
  <router-link :to="route" tag="li">
    <div class="v-left-item text-overflow">
      <vc-icon class="icon" :type="type"></vc-icon>
      <slot></slot>
    </div>
  </router-link>
</template>

<script type='text/ecmascript-6'>

export default {
  props: {
    type: {
      type: String,
      default: ''
    },
    route: {
      type: String,
      default: '/'
    }
  }
}
</script>

<style lang='stylus' rel='stylesheet/stylus' scoped>
@import "../styl/constant.styl"
.router-link-active
  border-right: 3px solid lightAlphaColor
  color darkAlphaColor
  margin-right: -2px
.v-left-item
  height: 45px
  width: 100%
  line-height: 45px
  text-align: center
  transition: all 0.5s
  cursor: pointer
  .icon
    float: left
    margin: 0 -14% 0 16%
    position: relative
    top: 2px
  &:hover
    background: lightBorder
</style>



